$time-trans: 0.3s;

.burger{
    padding: 25px;
    position: absolute;
    top: 5px;
    right: 5%;
    z-index: 10;
    &__el{
        height: 1px;
        width: 25px;
        background-color: $c-light;
        position: absolute;
        right: 5%;
        &:before{
            content: '';
            height: 1px;
            width: 25px;
            background-color: $c-light;
            position: absolute;
            top: -7px;
            transition: 0.5s margin-top ease;
            transition: 0.5s transform ease;
        }
        &:after{
            content: '';
            height: 1px;
            width: 25px;
            background-color: $c-light;
            position: absolute;
            top: 7px;
            transition: $time-trans margin-top ease;
            transition: $time-trans transform ease;
        }
        // transition: 0.3s width ease;
    }
}
.cross{
    width: 0;
    // transition: 0.3s width ease;
    &:before{
        content: '';
        transform: rotate(45deg);
        margin-top: 7px;
        margin-left: -25px;
        opacity: 1;
        transition: $time-trans margin-top ease;
        transition: $time-trans transform ease;
    }
    &:after{
        transform: rotate(-45deg);
        margin-top:  -7px;
        margin-left: -25px;
        opacity: 1;
        transition: $time-trans margin-top ease;
        transition: $time-trans transform ease;
    }
}
.menu{
    width: 100vw;
    height: 100vh;
    background-color: $c-dark-alpha;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
    h2{
        color: $c-logo;
        text-align: center;
    }
    ul{
        padding: 0;
        list-style: none;
        margin-top: 100px;
        .connected{
            p{
                color: $c-light;
                font-size: 1.8rem;
                margin-top: 0;
                margin-bottom: 15px;
            }
            a{
                &:before{
                    content: '';
                    display: none;
                }
            }
        }
        li{
            text-align: center;
            padding: 20px;
            position: relative;
            .active{
                color: $c-logo;
                &:before{
                    content: '>';
                    color: grey;
                    // font-size: 1rem;
                    margin-right: 10vw;
                    line-height: 0.4;
                }
                &:after{
                    content: '<';
                    color: grey;
                    // font-size: 1rem;
                    margin-left: 10vw;
                    line-height: 0.4;
                }
            }
            a{
                color: $c-light;
                text-decoration: none;
                text-transform: uppercase;
                font-size: 1.8rem;
                display: block;
                transition: 0.2s all ease;
                &:hover{
                    color: $c-btn;
                    transition: 0.2s all ease;
                }
            }
            &:before{
                content: '';
                // border-top: 2px solid red;
                width: 80%;
                height: 1px;
                background-color: grey;
                position: absolute;
                left: 50%;
                transform: translate(-50%);
                margin-top: -20px;
            }
            &:first-child:before{
                display: none;
            }
            &:last-child{
                background-color: red;
                border-radius: 20px;
                margin-top: 30px;
                background-color: rgba(80,80,80,0.6);
                &:before{
                    margin-top: -50px;
                }
            }
            .main-link{
                background-color: $c-logo;
                // background-image: url('../../assets/images/bg-jungle03.png');
                // background-size: cover;
                border-radius: 10px;
                padding-top: 4px;
                width: 80%;
                margin-left: 10%;
                &:before{
                    content: 'ou';
                    position: absolute;
                    left: 50%;
                    transform: translate(-50%);
                    margin-top: 52.5px;
                    font-size: 1rem;
                    color: grey
                }
                &:hover{
                    box-shadow: $s-btn-hover;
                    color: #e3e3e3;
                    background-color: #e3ac05;
                    transition: 0.2s all;
                }
            }
            .connection-link{
                color: lightgrey;
                font-size: 1.2rem;
                margin-top: 30px;
                text-decoration: underline;
                text-decoration-color: grey;
            }
        }
    }
    transition: 0.3s all ease;
}
.slide{
    transform: translateX(0%);
    transition: 0.5s all ease;
}

.nav-footer{
    ul{
        list-style: none;
        padding: 0;
        font-size: 0.8rem;
        text-align: right;
        margin: 0;
        margin-top: -50px;
        li{
            display: inline;
            margin-left: 15px;
            a{
                color: $f-light;
                text-decoration: none;
                color: $f-footer;
                &:hover{
                    color: $c-logo;
                    transition: 0.2s all;
                }
            }
            &:after{
                content:'-';
                position: absolute;
                margin-left: 4px;
                color: $c-logo;
            }
            &:last-child:after{
                content:'';
            }
        }
    }
}


@media (min-width: 576px) {
    .nav-footer{
        ul{
            font-size: 1.2rem;
            margin-top: -60px;
        }
    }
}
@media (min-width: 768px) {
    .nav-footer{
        ul{
            margin-top: -75px;
        }
    }
}
@media (min-width: 992px) {
    .menu{
        width: 80%;
        float: right;
        // text-align: right;
        height: inherit;
        background-color: inherit;
        position: relative;
        top: inherit;
        right: inherit;
        transform: none;
        display: inline;
        margin: 0;
        padding: 0;
        float: right;
        &--game{
            width: 40%;
            nav ul li{
                margin: 0;
                margin-left: 15px;
                margin-top: 5px;
                padding: 0;
                opacity: 0.9;
                transition: all 0.2s ease;
                &:hover{
                    opacity: 1;
                    transition: all 0.2s ease;
                }
                img{
                    width: 60px;
                }
                &:last-child{
                    a{
                        margin-right: 0px;
                    }
                    background-color: inherit;
                    margin-top: 5px;
                    margin-left: 15px;
                    padding: 0;
                    &:before{
                        margin-top: -50px;
                    }
                }
            }
            &:last-child{
                background-color: inherit;
                margin-top: 0px;
                padding: 10px;
                &:before{
                    margin-top: -50px;
                }
            }
        }
        ul{
            padding: 0;
            list-style: none;
            margin-top: 0;
            position: absolute;
            right: 0;
            .connected:last-child{
                background-color: inherit;
                margin-top: -5px;
                margin-left: 60px;
                p{
                    color: $c-light;
                    font-size: 1.2rem;
                    margin: 0;
                    &:before{
                        content: '';
                        width: 1px;
                        height: 60px;
                        margin-left: -30px;
                        margin-top: 5px;
                        background-color: grey;
                        position: absolute;
                    }
                }
                a{
                    margin-left: 0;
                    padding: 0 25px;
                    border-radius: 2px;

                    &:before{
                        content: '';
                        display: none;
                    }
                }
            }
            li{
                text-align: inherit;
                padding: 20px;
                position: relative;
                width: inherit;
                float: left;
                .active{
                    color: $c-logo;
                    &:before, &:after{
                        display: none;
                    }
                }
                a{
                    color: $c-light;
                    text-decoration: none;
                    text-transform: inherit;
                    font-size: 1.2rem;
                    display: inline;
                    width: inherit;
                }
                &:before{
                    display: none;
                }
                &:first-child:before{
                    display: none;
                }
                &:last-child{
                    a{
                        margin-right: 10px;
                    }
                    background-color: #2e2e2e;
                    border-radius: 5px;
                    margin-top: 12px;
                    margin-left: 10px;
                    padding: 10px;
                    &:before{
                        margin-top: -50px;
                    }
                }
                .main-link{
                    background-color: $c-logo;
                    border-radius: 5px;
                    padding: 3px 6px;
                    width: inherit;
                    margin-left: inherit;
                    &:before{
                        display: none;
                    }
                }
                .connection-link{
                    color: lightgrey;
                    font-size: 1.2rem;
                    margin-top: inherit;
                    text-decoration: underline;
                    text-decoration-color: grey;
                }
            }
        }
        transition: 0.3s all ease;
    }
    .nav-footer{
        ul{
            margin-top: -65px;
            li{
                margin-left: 35px;
                &:after{
                    content:'-';
                    position: absolute;
                    margin-left: 12px;
                    color: $c-logo;
                }
            }
        }
    }
}
@media (min-width: 1200px) {
    .nav-footer{
        ul{
            margin-top: -70px;
            // font-size: 1.4rem;
            li{
                margin-left: 35px;
                &:after{
                    content:'-';
                    position: absolute;
                    margin-left: 12px;
                    color: $c-logo;
                }
            }
        }
    }
}
