img{
    max-width: 100%;
    margin-bottom: 25px;
}
.bg-img{
    background-color: $bg-img;
    border-radius: 5px;
}
.img-center{
    margin: auto;
    display: block;
}

@media (min-width: 768px) {
    .tablet-img{
        max-width: 60%;
        margin: auto;
        display: block;
    }
}
@media (min-width: 992px) {
    .marge-img{
        margin-top: 100px;
    }
}
