footer{
    padding-top: 10px;
    padding-bottom: 10px;
    p{
        color: $f-footer;
        font-size: 0.8rem;
        margin-bottom: 0;
        text-align: center;
    }
}
.h2-logo{
    display: inline;
    a{
        img{
            width: 15vw;
            margin-top: 10px;
            margin-bottom: 0;
        }
    }
}
.footer-game{
    background-color: transparent;
    padding: 0;
    p{
        margin-top: 50px;
        width: inherit;
        display: inline;
        float: right;
        text-align: right;
        color: $c-main-dark;
        a{
            color: $c-main-dark;
        }
    }
}
@media (min-width: 576px) {
    footer{
        p{
            color: $f-footer;
            font-size: 1rem;
        }
    }
    .h2-logo{
        display: inline;
        a{
            img{
                margin-top: 15px;
                width: 15vw;
            }
        }
    }
}
@media (min-width: 992px) {
    footer{
        p{
            color: $f-footer;
            font-size: 1rem;
        }
    }
    .h2-logo{
        a{
            img{
                margin-top: 10px;
                width: 10vw;
            }
        }
    }
    .fix{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100vw;
    }
}
@media (min-width: 1200px) {
    footer{
        p{
            margin-top: 0;
        }
        padding-bottom: 15px;
    }
    .h2-logo{
        display: inline;
        a{
            img{
                width: 100px;
                margin-top: 5px;
                margin-bottom: 0;
            }
        }
    }
}
