html body{
    overflow-x: hidden;
    overflow-y: auto;
}
body {
    // position: relative;
    // width: 100vw;

    font-family: 'Baloo 2', sans-serif;
    font-size: 1.4rem;
    margin: 0;
    padding: 0;
}
.container{
    width: 90vw;
    margin: auto;
}
.margin-section{
    padding-top: 50px;
    padding-bottom: 75px;
}
.no-margin{
    margin: 0;
}
.clear{
    clear: both;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
.hidden{
    visibility: hidden;
    display: none;
}
.margin-title{
    margin-top: 90px;
}
.bottom{
    margin-bottom: 75px;
}

@media (min-width: 768px) {
    .margin-section{
        padding-top: 75px;
        padding-bottom: 100px;
    }
    .col{
        width: 60%;
        margin-left: auto;
        margin-right: auto;
    }
}
@media (min-width: 992px) {
    .container{
        width: 80%;
        margin: auto;
    }
    .mobile{
        display: none;
    }
    .col--left{
        float: left;
        width: 50%;
    }
    .col--right{
        float: right;
        width: 40%;
    }

    .margin-p{
        padding-top: 75px;
    }
}
@media (min-width: 1200px) {
    .container{
        width: 1150px;
        margin: auto;
    }
    .col{
        width: 40%;
        &--left{
            float: left;
            width: 50%;
        }
        &--img{
            width: 60%;
        }
        &--obj{
            float: left;
            width: 30%;
        }
        &--large{
            width: 60%;
        }
        &--pawn{
            width: 35%;
        }
    }
}
