.btn-play{
    background-color: $c-logo;
    padding: 0px 12vw;
    color: $f-light;
    text-decoration: none;
    font-size: 2rem;
    font-weight: bold;
    box-shadow: $s-btn;
    z-index: 10;
    border-radius: 5px;
    transition: 0.2s all;
    &--main{
        margin-top: 50px;
        background-color: red;
    }
    &:hover{
        box-shadow: $s-btn-hover;
        color: #e3e3e3;
        background-color: #e3ac05;
        transition: 0.2s all;
    }
}
.btn-link{
    float: right;
    text-align: right;
    box-shadow: $s-btn;
    padding: 2px 20px;
    color: $c-light;
    border-radius: 5px;
    text-decoration: none;
    background-color: $c-logo;
    &:hover{
        box-shadow: $s-btn-hover;
        color: #e3e3e3;
        background-color: #e3ac05;
        transition: 0.2s all;
    }
}
.list-btn{
    list-style: none;
    text-align: center;
    padding: inherit;
    &__el{
        margin-bottom: 50px;
    }
}
.btn-game{
    background-color: $c-btn;
    padding: 8px 40px;
    border-radius: 10px;
    font-size: 1.8rem;
    color: $c-light;
    text-decoration: none;
    border: 5px solid $c-logo;
    box-shadow: $s-btn;

    transition: 0.2s all;
    &:hover{
        background-color: $c-btn-hover;
        border-color: #e3ac05;
        box-shadow: 0px 2px 0px rgba( 0, 0, 0, 0.4);
        color: #e3e3e3;

        transition: 0.2s all;
    }
    &--ready{
        text-transform: uppercase;
        font-weight: bold;
        font-size: 2.2rem;
        padding: 4px 100px;
    }
    &--second{
        padding: 2px 40px;
        border: 4px solid #b0d980;
        &:hover{
            border-color: $c-btn;
        }
    }
}
.p-game{
    background-color: $c-btn;
    padding: 4px 18px;
    margin-left: 12px;
    border-radius: 10px;
    font-size: 1.8rem;
    display: inline-block;
    color: $c-light;
    text-decoration: none;
    border: 5px solid $c-logo;
    box-shadow: $s-btn;
    span{
        font-size: 2.2rem;
    }
}
.ready{
    float: right;
    margin: auto;
}
.play{
    text-align: center;
    &--main{
        padding-top: 65vh;
    }
}
.credits{
    font-size: 1rem;
}

@media (min-width: 576px) {
    .play{
        &--main{
            padding-top: 70vh;
        }
    }
}
@media (min-width: 992px) {
    .btn-play{
        background-color: $c-logo;
        padding: 0px 12vw;
        color: $f-light;
        text-decoration: none;
        font-size: 2rem;
        font-weight: bold;
        box-shadow: $s-btn;
        z-index: 10;
        border-radius: 5px;
        &--main{
            margin-top: 50px;
            background-color: red;
        }
    }
    .play{
        margin-top: 50px;
        &--main{
            margin-top: 0px;
            position: absolute;
            left: 50%;
            top: 75vh;
            transform: translate(-50%);
            padding-top: inherit;
        }
    }
}
@media (min-width: 1200px) {
    .btn-play{
        padding: 0px 60px;
    }
}
