.area{
    background-color: #e0dba6;
    padding: 20px 35px;
    border-radius: 5px;
    border: 1px solid $c-main-dark;

    label{
        font-size: 1rem;
    }
    input{
        width: 100%;
        padding: 7px;
        margin-bottom: 20px;
        box-sizing: border-box;
        border-radius: 5px;
        border: 1px solid $c-main-dark;
    }
    .submit{
        background-color: $c-logo;
        padding: 10px 0px;
        box-sizing: inherit;
        margin-top: 25px;
    }
    .suppr{
        width: 100%;
        padding: 7px;
        margin-bottom: 20px;
        box-sizing: border-box;
        border-radius: 5px;
        border: 1px solid $c-main-dark;
        background-color: lightpink;
        padding: 10px 0px;
        box-sizing: inherit;
        margin-top: 0px;
    }
}
.go-register{
    font-size: 1rem;
    margin: 0;
    color: $f-footer;
    a{
        color: inherit;
    }
}

.pdp{
    display: flex;
    // flex-wrap: wrap;
    label{
        display: inline;
        input {
            display: none;
        }
        img {
            border: 4px solid transparent;
            border-radius: 7px;
            max-width: 90%;
        }
        input:checked + img {
            border: 4px solid #06c462;
            border-radius: 7px;
        }
    }
}
