h1{
    padding-top: 10px;
    margin: 0;
    z-index: 2;
    font-size: 0;
    a{
        img{
            width: 40vw;
            margin-left: 25vw;
            transition: all .3s;
        }
        .tiny-logo{
            width: 28vw;
            margin-left: 0vw;
            transition: all .5s;
        }
    }
}
h2, h3, h4{
    font-family: 'Lato', cursive;
    color: $c-dark;
    line-height: 1.2;
}
h2{
    font-size: 43.95px
}
h3{
    font-size: 35.16px;
    margin-top: 36px;
    line-height: 1;
}
h4{
    font-size: 28.13px;
}
p{
    color: $c-dark;
}
h4{
    margin-bottom: 0px;
    color: $c-light;
}
.text-list{
    list-style: none;
    padding: 20px;
    box-sizing: border-box;
    background-color: $c-logo-alpha;
    border: 1px solid $c-main-dark;
    border-radius: 5px;
    li p, li h4{
        color: $c-dark;
    }
}

@media (min-width: 768px) {
    h1{
        a{
            img{
                width: 24%;
                margin-left: 38%;
            }
            .tiny-logo{
                width: 20%;
            }
        }
    }
    .list{
        padding: 0;
    }
}
@media (min-width: 992px) {
    h1{
        display: inline-block;
        width: 20%;
        float: left;
        a{
            img{
                width: 100%;
                margin-left: 0;
            }
            .tiny-logo{
                width: 80%;
            }
        }
    }
}
@media (min-width: 1200px) {
    h1{
        width: 250px;
        a{
            img{
                width: 100%;
                margin-left: 0;
            }
            .tiny-logo{
                width: 60%;
            }
        }
    }
    .text-list{
        width: 130%;
        margin-left: -15%;
        padding: inherit;
        box-sizing: inherit;
        background-color: inherit;
        border: inherit;
        display: inline-flex;
        flex-wrap: wrap;
        li p, li h4{
            color: $c-dark;
        }
        li h4{
            margin-top: 5px;
        }
        &__el{
            flex-wrap: wrap;
            margin: 10px;
            width: calc(30% - 40px);
            padding: 20px;
            border-radius: 5px;
            background-color: $c-logo-dark;
            border: 1px solid $c-dark;
            box-shadow: $s-btn;
            &:first-child{
                margin-left: 0;
            }
            &:nth-child(4){
                margin-left: 0;
            }
            &:last-child{
                margin-right: 0;
            }
            &:nth-child(3){
                margin-right: 0;
            }
            &--chance{
                background-color: #ebcca9;
            }
            &--money{
                background-color: #bceba9;
            }
            &--resources{
                background-color: #e3e3e3;
            }
            &--attack{
                background-color: #eba9a7;
            }
            &--bank{
                background-color: #a7c1eb;
            }
            &--benefit{
                background-color: #a7e0eb;
            }
        }
    }
}
