.scroll{
    // display: flex;
    margin-top: 25px;
    margin-left: 48%;
    justify-content: center;
    align-items: center;
    // z-index: 10;
    .scroll-arrow {
        z-index: 10;
      width: $arrow-size;
      height: $arrow-size;
      transform: rotate(45deg);
      border-right: 2px solid $c-light;
      border-bottom: 2px solid $c-light;
      animation: arrow-wave 1s infinite;
      animation-direction: alternate;
      @for $i from 1 through 3 {
        &:nth-child(#{$i}) {
          animation-delay: $animation-delay*$i;
        }
      }
    }

    @keyframes arrow-wave {
      0% {
        opacity: 0;
      }
      50% {
        opacity: .5;
      }
      100% {
        opacity: 1;
      }
    }

}
@media (min-width: 992px) {
    .scroll{
        position: absolute;
        top: 90vh;
        left: 50%;
        margin: auto;
        transform: translate(-50%);
    }
}
