.table-of-contents{
    background-color: #e0dba6;
    padding: 35px;
    border-radius: 5px;
    border: 1px solid #b3ae76;
    a{
        color: inherit;
        text-decoration: inherit;
    }
    &__main-list{
        padding: 0;
        ol{
            color: #858052;
            font-size: 1.2rem;
            a{
                color: inherit;
            }
            li{
                margin-bottom: 0;
                &:before{
                    display: none;
                }
            }
        }
        li{
            margin-bottom: 12px;
            &:before{
                content: '';
                // border-top: 2px solid red;
                width: 80%;
                height: 1px;
                background-color: #b3ae76;
                position: absolute;
                left: 50%;
                transform: translate(-50%);
                margin-top: -6px;
            }
            &:first-child:before{
                display: none;
            }
        }
    }
    h3{
        margin-top: 0;
    }
}
@media (min-width: 1200px) {
    .table-of-contents__main-list{
        li{
            position: relative;
            &:before{
                content: '';
                width: 100%;
                height: 1px;
                background-color: #b3ae76;
                position: absolute;
                left: 0%;
                transform: inherit;
                margin-top: -6px;
            }
            &:first-child:before{
                display: none;
            }
        }
    }
}
