header .infos-player{
    background-image: url("/assets/images/profil.png");
    // background-color: brown;
    background-size: cover;

    width: 25%;
    height: 125px;
    display: flex;
    float: left;
    margin-top: 10px;
    img{
        width: 25%;
        float: left;
        margin-left: 36px;
        margin-top: 28px;
        box-shadow: $s-btn;
        border-radius: 5px;
    }
    &__el{
        margin-top: 28px;
        p{
            text-align: left;
            float: left;
            width: 70%;
            margin: 0;
            margin-left: 10px;
            color: $c-light;
            font-size: 1rem;
        }
        .pseudo{
            font-weight: bold;
            font-size: 1.6rem;
        }
    }
}

#slider {
  position: relative;
  overflow: hidden;
  margin: 50px 0 50px 0;
  border-radius: 5px;
  box-shadow: $s-btn;
  border: 5px solid $c-wood-border;
}

#slider ul {
  position: relative;
  margin: 0;
  padding: 0;
  height: 200px;
  width: 100%;
  list-style: none;
}

#slider ul li {
  position: relative;
  display: block;
  float: left;
  margin: 0;
  padding: 0;
  width: 402.5px;
  height: 400px;
  background: $c-btn;
  text-align: center;
  line-height: 300px;
  img{
      width: 40%;
      margin-top: 20px;
  }
}

a.control_prev, a.control_next {
  position: absolute;
  top: 40%;
  z-index: 999;
  display: block;
  padding: 4% 3%;
  width: 11px;
  height: 22px;
  padding-left: 20px;
  box-sizing: inherit;
  background-image: url("/assets/images/arrow.png");
  background-repeat: no-repeat;
  background-size: contain;
  text-decoration: none;
  font-weight: 600;
  font-size: 18px;
  opacity: 0.6;
  cursor: pointer;

  transition: all 0.2s ease;
}
a.control_prev{
    transform: rotate(180deg);
}

a.control_prev:hover, a.control_next:hover {
  opacity: 1;
  transition: all 0.2s ease;
}

a.control_prev {
  border-radius: 0 2px 2px 0;
}

a.control_next {
  right: 0;
  border-radius: 2px 0 0 2px;
}

.slider_option {
  position: relative;
  margin: 10px auto;
  width: 160px;
  font-size: 18px;
}
.list-player{
    background-color: $c-wood;
    border: 5px solid $c-wood-border;
    box-sizing: border-box;
    width: 100%;
    height: 530px;
    box-shadow: $s-btn;
    border-radius: 5px;
    margin-top: 50px;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    &__el{
        background-color: $c-btn;
        width: 90%;
        margin-left: 5%;
        border-radius: 5px;
        margin-top: 16px;
        display: inline-block;
        border: 4px solid #b0d980;
        box-sizing: border-box;
        box-shadow: $s-btn;
        img{
            width: 80px;
            box-shadow: $s-btn;
            border-radius: 5px;
            margin: 10px;
            margin-bottom: 0;
        }
        .player{
            float: right;
            width: 70%;
            p{
                margin: 0;
                color: $c-light;
                &:first-child{
                    margin-top: 10px;
                    font-weight: bold;
                    font-size: 1.8rem;
                }
            }
        }
    }
}
