.text-list {
  list-style: none;
  padding: 20px;
  box-sizing: border-box;
  background-color: rgba(197, 191, 137, 0.9);
  border: 1px solid #aba56f;
  border-radius: 5px;
}
.text-list li p, .text-list li h4 {
