    width: 100%;
    margin-left: 0;
  }
  h1 a .tiny-logo {
    width: 60%;
  }

  .text-list {
    width: 130%;
    margin-left: -15%;
    padding: inherit;
    box-sizing: inherit;
    background-color: inherit;
    border: inherit;
    display: inline-flex;
    flex-wrap: wrap;
  }
  .text-list li p, .text-list li h4 {
    color: #202020;
  }
  .text-list li h4 {
    margin-top: 5px;
  }
  .text-list__el {
    flex-wrap: wrap;
    margin: 10px;
    width: calc(30% - 40px);
    padding: 20px;
    border-radius: 5px;
    background-color: rgba(204, 157, 14, 0.9);
    border: 1px solid #202020;
    box-shadow: 0px 4px 0px rgba(0, 0, 0, 0.4);
  }
  .text-list__el:first-child {
    margin-left: 0;
  }
  .text-list__el:nth-child(4) {
    margin-left: 0;
  }
  .text-list__el:last-child {
    margin-right: 0;
  }
  .text-list__el:nth-child(3) {
    margin-right: 0;
  }
  .text-list__el--chance {
    background-color: #ebcca9;
  }
  .text-list__el--money {
    background-color: #bceba9;
  }
  .text-list__el--resources {
    background-color: #e3e3e3;
  }
  .text-list__el--attack {
