.btn-play {
  background-color: #ffc001;
  padding: 0px 12vw;
  color: #fff;
  text-decoration: none;
  font-size: 2rem;
  font-weight: bold;
  box-shadow: 0px 4px 0px rgba(0, 0, 0, 0.4);
  z-index: 10;
  border-radius: 5px;
  transition: 0.2s all;
}
.btn-play--main {
  margin-top: 50px;
  background-color: red;
}
.btn-play:hover {
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.4);
  color: #e3e3e3;
  background-color: #e3ac05;
  transition: 0.2s all;
}

.btn-link {
  float: right;
  text-align: right;
  box-shadow: 0px 4px 0px rgba(0, 0, 0, 0.4);
  padding: 2px 20px;
  color: #fff;
  border-radius: 5px;
  text-decoration: none;
  background-color: #ffc001;
}
.btn-link:hover {
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.4);
  color: #e3e3e3;
