.burger {
  padding: 25px;
  position: absolute;
  top: 5px;
  right: 5%;
  z-index: 10;
}
.burger__el {
  height: 1px;
  width: 25px;
  background-color: #fff;
  position: absolute;
  right: 5%;
}
.burger__el:before {
  content: "";
  height: 1px;
  width: 25px;
  background-color: #fff;
  position: absolute;
  top: -7px;
  transition: 0.5s margin-top ease;
  transition: 0.5s transform ease;
}
.burger__el:after {
  content: "";
  height: 1px;
  width: 25px;
  background-color: #fff;
  position: absolute;
  top: 7px;
  transition: 0.3s margin-top ease;
  transition: 0.3s transform ease;
}

.cross {
  width: 0;
}
.cross:before {
  content: "";
  transform: rotate(45deg);
  margin-top: 7px;
  margin-left: -25px;
  opacity: 1;
  transition: 0.3s margin-top ease;
  transition: 0.3s transform ease;
}
.cross:after {
  transform: rotate(-45deg);
  margin-top: -7px;
  margin-left: -25px;
  opacity: 1;
  transition: 0.3s margin-top ease;
  transition: 0.3s transform ease;
}

.menu {
  width: 100vw;
  height: 100vh;
  background-color: rgba(20, 20, 20, 0.9);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  transition: 0.3s all ease;
}
.menu h2 {
  color: #ffc001;
  text-align: center;
}
.menu ul {
  padding: 0;
  list-style: none;
  margin-top: 100px;
}
.menu ul .connected p {
  color: #fff;
  font-size: 1.8rem;
  margin-top: 0;
  margin-bottom: 15px;
}
.menu ul .connected a:before {
  content: "";
  display: none;
}
.menu ul li {
  text-align: center;
  padding: 20px;
  position: relative;
}
.menu ul li .active {
  color: #ffc001;
}
.menu ul li .active:before {
  content: ">";
  color: grey;
  margin-right: 10vw;
  line-height: 0.4;
}
.menu ul li .active:after {
  content: "<";
  color: grey;
  margin-left: 10vw;
  line-height: 0.4;
}
.menu ul li a {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.8rem;
  display: block;
  transition: 0.2s all ease;
}
.menu ul li a:hover {
  color: #93B26E;
  transition: 0.2s all ease;
}
.menu ul li:before {
  content: "";
  width: 80%;
  height: 1px;
  background-color: grey;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  margin-top: -20px;
}
.menu ul li:first-child:before {
  display: none;
}
.menu ul li:last-child {
  background-color: red;
  border-radius: 20px;
  margin-top: 30px;
  background-color: rgba(80, 80, 80, 0.6);
}
.menu ul li:last-child:before {
