.table-of-contents__main-list ol {
  color: #858052;
  font-size: 1.2rem;
}
.table-of-contents__main-list ol a {
  color: inherit;
}
.table-of-contents__main-list ol li {
  margin-bottom: 0;
}
.table-of-contents__main-list ol li:before {
  display: none;
}
.table-of-contents__main-list li {
  margin-bottom: 12px;
}
.table-of-contents__main-list li:before {
  content: "";
  width: 80%;
  height: 1px;
  background-color: #b3ae76;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  margin-top: -6px;
}
.table-of-contents__main-list li:first-child:before {
  display: none;
}
.table-of-contents h3 {
  margin-top: 0;
}

@media (min-width: 1200px) {
  .table-of-contents__main-list li {
    position: relative;
  }
  .table-of-contents__main-list li:before {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #b3ae76;
    position: absolute;
    left: 0%;
    transform: inherit;
    margin-top: -6px;
  }
  .table-of-contents__main-list li:first-child:before {
    display: none;
  }
}
