video{
    position: absolute;
    max-width: 100%;
    top: 25vh;
    left: 0;
    z-index: -1;
}

@media (min-width: 768px) {
    video{
        top: 20vh;
    }
}
@media (min-width: 992px) {
    .video-wrapper{

    }
    video{
        position: absolute; right: 0; top: 0;
        min-width: 100%; min-height: 100%;
        width: auto; height: auto; z-index: -100;
        // background: url(polina.jpg) no-repeat;
        background-size: cover;
    }
}
